<template>
  <div id='map' ref='myMap'>
    <div class="tools">
      <el-row>
        <el-col :span="6">
          <el-button type="primary" icon="el-icon-location" v-on:click.native="drawPoint" circle></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="success" icon="el-icon-check" v-on:click.native="searchPoi" circle></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="info" icon="el-icon-delete" v-on:click.native="clear" circle></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="warning" icon="el-icon-edit-outline" v-on:click.native="findBestPlace" circle></el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import {
  createMap,
  createVector,
  createMarkers,
  createDraw
} from '../maputils/map.js'
import Feature from 'ol/feature'
import Point from 'ol/geom/point'
import { searchPoi } from '../maputils/poi.js'
export default {
  name: 'Map',
  mounted() {
    let target = this.$refs.myMap
    this.initMap(target)
    this.resize()
    document.body.onresize = this.resize.bind(this)
  },
  data() {
    return {
      msg: 'Hello Map'
    }
  },
  methods: {
    initMap(target) {
      let map = (this.map = createMap(target))
      let { vector, source } = createVector(map)
      let { markers } = createMarkers()
      this.vector = vector
      this.source = source
      this.markers = markers
      map.addLayer(vector)
      map.addLayer(markers)
    },
    resize() {
      let width = window.innerWidth
      let height = window.innerHeight
      this.map.setSize([width, height])
    },
    drawPoint() {
      this.draw = createDraw(this.map, this.source)
    },
    searchPoi() {
      if (this.draw) {
        this.draw.setActive(false)
      }
      searchPoi(this.source.getFeatures()).then(data => {
        this.markers.getSource().clear()
        let pois = data.pois
        for (let i in pois) {
          let poi = pois[i].location
          let xy = poi.split(',')
          let point = new Point([+xy[0], +xy[1]])
          point = point.transform('EPSG:4326', 'EPSG:3857')
          let feature = new Feature({
            geometry: point,
            labelPoint: point.clone(),
            name: pois[i].name
          })
          this.markers.getSource().addFeature(feature)
        }
      })
    },
    clear() {
      if (this.draw) {
        this.draw.setActive(false)
      }
      this.source.clear()
    },
    findBestPlace() {
      if (this.draw) {
        this.draw.setActive(false)
      }
    }
  }
}
</script>
<style scoped>
#map {
  width: 100%;
}
.el-row {
  line-height: 60px;
}
.tools {
  position: absolute;
  top: 8.5em;
  left: 0.5em;
  z-index: 999;
}
</style>



